let other_rooms_list = document.querySelectorAll(".other-rooms-list__item");

other_rooms_list.forEach(function (link) {
  link.addEventListener("click", function (event) {
    if (document.documentElement.clientWidth > 768) {
      event.preventDefault();
    }
    let prev_room = document.querySelector(".other-rooms-preview-list__item--active");
    let prev_link = document.querySelector(".other-rooms-list__item--active");
    let room = document.querySelector(link.getAttribute("href"));
    room.classList.toggle("other-rooms-preview-list__item--active");
    prev_room.classList.toggle("other-rooms-preview-list__item--active");
    prev_link.classList.toggle("other-rooms-list__item--active");
    link.classList.toggle("other-rooms-list__item--active");
  });
});

let other_rooms_detail = document.querySelectorAll('.other-rooms-preview-info-detail');
other_rooms_detail.forEach(el => el.addEventListener('click', function(event) {
  el.classList.toggle("other-rooms-preview-info-detail--active"); 
}))