"use strict";

//import { tns } from "tiny-slider";
import { tns } from "../../../../node_modules/tiny-slider/src/tiny-slider";
if (document.querySelector(".gallery-slider")) {
  const gallery_slider = tns({
    container: ".gallery-slider",
    items: 1,
    slideBy: "page",
    autoplay: true,
    touch: true,
    mouseDrag: true,
    //destroy: destroy(),
    prevButton: ".gallery-slider__prev", 
    nextButton: ".gallery-slider__next",
    controlsContainer: ".gallery-slider-controls",
  });
}

let gallery_slider_icon = document.querySelectorAll(".gallery-slider__icon");

  gallery_slider_icon.forEach(function (icon) {
    icon.addEventListener("click", function (event) {
      event.preventDefault();
      let prev_icon = document.querySelector(".gallery-slider__icon--active");
      let gallery_slider_item = document.querySelector(".gallery-slider");
      let gallery_slider_row = document.querySelector(".gallery-slider-row");
      document.querySelector(".gallery-slider-controls").classList.toggle("gallery-slider-controls--module");
      if (gallery_slider_item) {
        gallery_slider_item.classList.remove("gallery-slider");
        gallery_slider_item.classList.add("gallery-slider-row");
        //console.log(gallery_slider_item.classList); 
        //gallery_slider.destroy();
      } else if (gallery_slider_row) {
        gallery_slider_row.classList.remove("gallery-slider-row");
        gallery_slider_row.classList.add("gallery-slider");
        //gallery_slider.rebuild();
      }

      icon.classList.toggle("gallery-slider__icon--active");
      prev_icon.classList.toggle("gallery-slider__icon--active");
    });
  });